.banner{
    position: relative;
    padding: 24vh 1%;

    .slide_banner{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        max-width: 100% !important;
        height: 125vh;

        .selected{
            opacity: 1;
        }

        img{
            object-position: top;
            position: absolute;
            transition: 1s;
            left: 0;
            top: 0;
        }

    }


}

@media only screen and (orientation: portrait) {    
    .banner{
        height: 100vh !important;
        background-image: url('../../public/images/1_mod_ret.jpg');
        background-position: center;
        background-size: cover;
    }

    .slide_banner{
        display: none;
    }
    
}

@media (max-width: 780px){
    .banner{
        background-size: cover;
        padding: 24vh 1%;

    }

    .tablet_resolution{
        display: block !important;
    }
}

@media (max-width: 500px){
    .banner{
        height: 100vh !important;
        background-image: url('../../public/images/mob_one.png');
    }

    .slide_banner{
        display: none;
    }
}
